import React, { Component } from 'react'
import {Tabs,Tab,Grid,Cell,Card,CardText,CardTitle,CardActions,Button,CardMenu,IconButton}from 'react-mdl';
 class Projects extends Component {
     constructor(props){
         super(props);
         this.state={activeTab:0};

     }
     toggleCategories(){
        if(this.state.activeTab === 0){
            return(
                <div className="projects-grid">
               <Card shadow={5}style={{minWidth:'450',margin:'auto'}}>
             <CardTitle style={{color:'#fff',height:'250px',background:'url(data:image/jpeg;base64,)center/cover'}}> Project #1 </CardTitle>
               <CardText>A HealthBioApp that helps you to be able to know more about the symptoms,causes and prevention of various chhronic diseases.</CardText>
              <CardActions border>
              <Button colored><a href="https://github.com/Dianaiminza/Health-Bio-App">Github</a></Button>
              <Button colored><a href="https://dianaiminza.github.io/Health-Bio-App/">Live Demo</a></Button>
              </CardActions>
              <CardMenu style={{color:'#fff'}}>
              <IconButton name="share"/>

              </CardMenu>
               </Card>
               <Card shadow={5}style={{minWidth:'450',margin:'auto'}}>
               <CardTitle style={{color:'#fff',height:'250px',background:'url(data:image/jpeg;base64,)center/cover'}}> Project #2 </CardTitle>
               <CardText>Kicks Kenya is an e-commerce website that helps people to be able to buy and order shoes</CardText>
              <CardActions border>
              <Button colored><a href="https://github.com/Dianaiminza/KicksKenya">Github</a></Button>
              <Button colored><a href="https://kickskenya-fa0f5.web.app">Live Demo</a></Button>
              </CardActions>
              <CardMenu style={{color:'#fff'}}>
              <IconButton name="share"/>
              
              </CardMenu>
               </Card>
               <Card shadow={5}style={{minWidth:'450',margin:'auto'}}>
             <CardTitle style={{color:'#fff',height:'250px',background:'url(data:image/jpeg;base64,)center/cover'}}> Project #3</CardTitle>
               <CardText>ChuckNorris App</CardText>
              <CardActions border>
              <Button colored><a href="https://github.com/Dianaiminza/ChuckNorris">Github</a></Button>
              <Button colored><a href=" https://dianaiminza.github.io/ChuckNorris/">Live Demo</a></Button>
              </CardActions>
              <CardMenu style={{color:'#fff'}}>
              <IconButton name="share"/>
              
              </CardMenu>
               </Card>
               </div>
            )  
          }
     }
     
    render() {
        return (
            <div className="category-tabs">
               <Tabs activeTab={this.state.activeTab}onChange={(tabId)=>this.setState({activeTab:tabId})}ripple ></Tabs>
            <Tab>REACT</Tab>
            
            <section className="projects-grid">
<Grid >
  <Cell col={12} >
      <div className="content">{this.toggleCategories()}</div>
  </Cell> 
</Grid>

            </section>
            </div>
        )
    }
}
export default Projects;
